import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { Button, Form, Modal } from "semantic-ui-react";
import modalActions from "../../actions/modalActions";
import agent from "../../agent";

export const PCComponentForm = ({ onSave, id = 0, record }) => {
  const dispatch = useDispatch();
  const modal = useSelector((state) => state.modal);

  const initialForm = {
    name: "",
  };

  const [formData, setFormData] = useState(
    id === 0 ? { ...initialForm } : { ...record }
  );

  const handleFormInput = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const saveForm = async () => {
    modalActions.setLoading(dispatch, true);

    try {
      if (id === 0) await agent.PCComponent.create({ ...formData });
      else await agent.PCComponent.update(id, { ...formData });

      if (onSave) onSave();

      toast("PC Component Created");
      modalActions.closeModal(dispatch);
    } catch (err) {
      modalActions.setErrors(dispatch, err.data.errors);
    }

    modalActions.setLoading(dispatch, false);
  };

  return (
    <>
      <Modal.Content>
        <Form>
          <Form.Field>
            <label>Name</label>
            <Form.Input
              name="name"
              value={formData.name}
              onChange={handleFormInput}
              placeholder="Name"
            ></Form.Input>
          </Form.Field>
        </Form>
      </Modal.Content>

      <Modal.Actions>
        <Button
          negative
          loading={modal.loading}
          disabled={modal.loading}
          onClick={() => {
            modalActions.closeModal(dispatch);
          }}
        >
          Cancel
        </Button>
        <Button
          positive
          loading={modal.loading}
          disabled={modal.loading}
          onClick={saveForm}
        >
          OK
        </Button>
      </Modal.Actions>
    </>
  );
};
