import React, { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import agent from "../../agent";
import { dateStringToLocal } from "../../helpers";
import { downloadBase64File } from "../../libs/download";
import { PopupButton } from "../Commons/PopupButton";

export const PurchaseOrderComponent = () => {
  const columns = [
    {
      name: "Item",
      selector: (row) => row.item_name,
      sortable: true,
    },
    {
      name: "For Room",
      selector: (row) => row.room_name,
      sortable: true,
    },
    {
      name: "Actions",
      selector: (row) => row.actions,
      sortable: true,
      right: true,
    },
  ];

  const [dtData, setDtData] = useState([]);
  const [loading, setLoading] = useState(false);

  const loadData = async () => {
    setLoading(true);

    let response = await agent.PurchaseOrders.list();

    response = response.map((a) => {
      return {
        item_name: a.item_name,
        room_name: a.room_name,
        actions: (
          <>
            <PopupButton
              content="Download Attached"
              iconName="cloud download"
              onClick={async () => {
                const response = await agent.FileStorage.get(a.file_storage_id);
                downloadBase64File(response.base64, response.name);
              }}
            />
          </>
        ),
      };
    });

    setDtData(response);

    setLoading(false);
  };

  useEffect(() => {
    loadData();
  }, []);

  return (
    <DataTable
      striped
      progressPending={loading}
      columns={columns}
      data={dtData}
    />
  );
};
