import React, { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import { Button, Icon, Loader, Segment } from "semantic-ui-react";
import { history } from "../..";
import agent from "../../agent";
import { DelayedSearchInput } from "../Commons/DelayedSearchInput";
import { PopupButton } from "../Commons/PopupButton";

export const RoomItemsComponent = () => {
  const { id } = useParams();

  const columns1 = [
    {
      name: "Item",
      selector: (row) => row.inventory_parent_item.name,
      sortable: true,
    },
    {
      name: "Type",
      selector: (row) => row.inventory_parent_item.item_type,
      sortable: true,
    },
    {
      name: "Brand",
      selector: (row) => row.brand,
      sortable: true,
    },
    {
      name: "Serial Number",
      selector: (row) => row.serial_number,
      sortable: true,
    },
    {
      name: "Action(s)",
      selector: (row) => (
        <>
          {loadingItems ? (
            <Icon name="spinner" color="green" loading />
          ) : (
            <PopupButton
              content="Remove"
              iconName="minus"
              color="teal"
              onClick={async () => {
                setLoadingItems(true);
                const response = await agent.Inventory.setRoom(row.id, {
                  room_id: null,
                });
                setDtAvailableItems([...dtAvailableItems, response]);
                setDtRoomItems(
                  dtRoomItems.filter((a) => a.id !== row.id)
                );

                
                setLoadingItems(false);
              }}
            />
          )}
        </>
      ),
      right: true,
    },
  ];
  const columns2 = [
    {
      name: "Item",
      selector: (row) => row.inventory_parent_item.name,
      sortable: true,
    },
    {
      name: "Type",
      selector: (row) => row.inventory_parent_item.item_type,
      sortable: true,
    },
    {
      name: "Brand",
      selector: (row) => row.brand,
      sortable: true,
    },
    {
      name: "Serial Number",
      selector: (row) => row.serial_number,
      sortable: true,
    },
    {
      name: "Action(s)",
      selector: (row) => (
        <>
          {loadingItems ? (
            <Icon name="spinner" color="green" loading />
          ) : (
            <PopupButton
              content="Add"
              iconName="add"
              color="teal"
              onClick={async () => {
                setLoadingItems(true);
                const response = await agent.Inventory.setRoom(row.id, {
                  room_id: id,
                });

                setDtRoomItems([...dtRoomItems, response]);
                setDtAvailableItems(
                  dtAvailableItems.filter((a) => a.id !== row.id)
                );
                setLoadingItems(false);
              }}
            />
          )}
        </>
      ),
      right: true,
    },
  ];

  const dispatch = useDispatch();

  const [loading, setLoading] = useState(false);
  const [loadingItems, setLoadingItems] = useState(false);

  const [room, setRoom] = useState({ name: "" });

  const [dtRoomItems, setDtRoomItems] = useState([]);
  const [dtAvailableItems, setDtAvailableItems] = useState([]);

  useEffect(() => {
    loadData();
    loadAvailableItems();
  }, []);

  const loadData = async () => {
    setLoading(true);
    const response = await agent.Room.allItems(id);
    setRoom({
      name: response.name,
    });
    setDtRoomItems(response.inventory_items);
    setLoading(false);
  };

  const loadAvailableItems = async () => {
    setLoadingItems(true);

    const response = await agent.Inventory.availableItems();
    setDtAvailableItems(response);

    setLoadingItems(false);
  };

  return (
    <>
      <div>
        <div className="page-header-title">
          <span
            className="link"
            onClick={() => {
              console.log("going back");
              history.goBack();
            }}
          >
            ROOMS
          </span>
          <Icon name="arrow right" />
          {room.name} <Loader active={loading} inline size="tiny" />
        </div>
        <hr></hr>
      </div>

      <Segment.Group>
        <Segment>
          <Icon name="boxes" />
          <b>ITEMS</b>
        </Segment>
        <Segment>
          <DataTable
            progressPending={loadingItems && loading}
            striped
            columns={columns1}
            data={dtRoomItems}
          />
        </Segment>
      </Segment.Group>

      <Segment.Group>
        <Segment>
          <Icon name="boxes" />
          <b>AVAILABLE ITEMS</b>
        </Segment>
        <Segment>
          <DataTable
            progressPending={loadingItems && loading}
            striped
            columns={columns2}
            data={dtAvailableItems}
          />
        </Segment>
      </Segment.Group>
    </>
  );
};
