import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { Button, Form, Modal } from "semantic-ui-react";
import modalActions from "../../actions/modalActions";
import agent from "../../agent";

export const JobOrderReplaceItemForm = ({
  itemParentId,
  requestedItemId,
  jobOrderId,
  onConfirm,
}) => {
  const dispatch = useDispatch();
  const modal = useSelector((state) => state.modal);

  const [items, setItems] = useState([]);
  const [loadingItems, setLoadingItems] = useState(true);
  const [selectedItem, setSelectedItem] = useState(0);

  const loadItems = async () => {
    modalActions.setLoading(dispatch, true);
    setLoadingItems(true);

    let response = await agent.Inventory.parentAvailableItems(itemParentId);
    response = response
      .filter((r) => r.id !== requestedItemId)
      .map((a) => {
        return {
          text: a.serial_number,
          value: a.id,
        };
      });

    setItems(response);

    if (response.length > 0) {
      setSelectedItem(response[0].value);
    }

    setLoadingItems(false);
    modalActions.setLoading(dispatch, false);
  };

  useEffect(() => {
    loadItems();
  }, []);

  return (
    <>
      <Modal.Content>
        {!loadingItems && (
          <>
            {items.length === 0 && (
              <p>
                No items found from the Inventory to replace the requested item.
              </p>
            )}
            <Form>
              <Form.Field>
                <label>Item From Inventory</label>
                <Form.Select
                  search
                  options={items}
                  value={selectedItem}
                  onChange={(e, data) => {
                    setSelectedItem(data.id);
                  }}
                />
              </Form.Field>
            </Form>
          </>
        )}
      </Modal.Content>
      <Modal.Actions>
        <Button
          negative
          loading={modal.loading}
          disabled={modal.loading}
          onClick={() => {
            modalActions.closeModal(dispatch);
          }}
        >
          Cancel
        </Button>
        <Button
          positive
          loading={modal.loading}
          disabled={modal.loading || items.length === 0}
          onClick={async () => {
            modalActions.setErrors(dispatch, null);
            modalActions.setLoading(dispatch, true);

            try {
              // request here
                await agent.JobOrders.replace(jobOrderId, {
                  replacement_item_id: selectedItem,
                });


              onConfirm();
              toast.success("Item has been replaced");
              modalActions.closeModal(dispatch);
            } catch (err) {
              modalActions.setErrors(dispatch, err.data.errors);
              modalActions.setLoading(dispatch, false);
            }
          }}
        >
          OK
        </Button>
      </Modal.Actions>
    </>
  );
};
