import React, { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import { useDispatch } from "react-redux";
import { useParams, useLocation } from "react-router-dom";
import { toast } from "react-toastify";
import { Button, Icon, Loader } from "semantic-ui-react";
import { history } from "../..";
import modalActions from "../../actions/modalActions";
import agent from "../../agent";
import { dateStringToLocal } from "../../helpers";
import { ConfirmationModal } from "../Commons/ConfirmationModal";
import { DelayedSearchInput } from "../Commons/DelayedSearchInput";
import { PopupButton } from "../Commons/PopupButton";
import { PCComponentInstanceForm } from "./PCComponentInstanceForm";

export const PCComponentInstanceContent = () => {
  const columns = [
    {
      name: "Serial Number",
      selector: (row) => row.serial_number,
      sortable: true,
    },
    {
      name: "Brand",
      selector: (row) => row.brand,
      sortable: true,
    },
    {
      name: "Date Created",
      selector: (row) => dateStringToLocal(row.created_at),
      sortable: true,
    },
    {
      name: "Actions",
      selector: (row) => (
        <>
          <PopupButton
            color="teal"
            content="Edit Item"
            iconName="pencil"
            onClick={() => {
              modalActions.openModal(
                dispatch,
                "Edit " + row.serial_number,
                <PCComponentInstanceForm
                  onSave={loadData}
                  id={row.id}
                  record={row}
                  componentId={id}
                />
              );
            }}
          />
          <PopupButton
            color="teal"
            content="Delete Item"
            iconName="trash"
            onClick={() => {
              modalActions.openModal(
                dispatch,
                "Delete " + row.serial_number,
                <ConfirmationModal
                  content="Confirm delete?"
                  onSubmit={async () => {
                    modalActions.setLoading(dispatch, true);

                    await agent.PCComponentInstance.delete(row.id);

                    toast.success("Deleted");
                    loadData();

                    modalActions.closeModal(dispatch);
                  }}
                />
              );
            }}
          />
        </>
      ),
      right: true,
    },
  ];

  const { id } = useParams();

  const dispatch = useDispatch();
  const [loading, setLoading] = useState(false);

  const [data, setData] = useState([]);
  const [dataTemp, setDataTemp] = useState([]);

  const [pcComponent, setPCComponent] = useState({ name: "", id: id });

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    setLoading(true);

    const response = await agent.PCComponent.show(id);

    setPCComponent({
      name: response.name,
      id: response.id,
    });

    setData(response.instances);
    setDataTemp(response.instances);

    setLoading(false);
  };

  const onSearch = (term) => {};

  const openForm = () => {
    modalActions.openModal(
      dispatch,
      "Create New Item",
      <PCComponentInstanceForm componentId={id} onSave={loadData} />
    );
  };

  return (
    <>
      <div>
        <div className="page-header-title">
          <span
            className="link"
            onClick={() => {
              history.push("/pc-components");
            }}
          >
            PC COMPONENT{" "}
          </span>
          <Icon name="arrow right" />
          {pcComponent.name} <Loader active={loading} inline size="tiny" />
        </div>
        <hr></hr>
      </div>

      <div className="mb-10 clearfix">
        <div className="disp-ib">
          <DelayedSearchInput onSearch={onSearch} />
        </div>
        <div className="float-r disp-ib">
          <Button
            size="small"
            color="green"
            onClick={() => {
              openForm();
            }}
          >
            <Icon name="add" /> Create Item
          </Button>
        </div>
      </div>

      <DataTable columns={columns} data={data} pagination striped />
    </>
  );
};
