import React, { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import { Button, Icon, Loader, Segment } from "semantic-ui-react";
import { history } from "../..";
import agent from "../../agent";
import { DelayedSearchInput } from "../Commons/DelayedSearchInput";
import { PopupButton } from "../Commons/PopupButton";

export const InventoryItemComponentsContent = () => {
  const { id } = useParams();

  const dispatch = useDispatch();

  const columns1 = [
    {
      name: "Component",
      selector: (row) => row.component.name,
      sortable: true,
    },
    {
      name: "Brand",
      selector: (row) => row.brand,
      sortable: true,
    },
    {
      name: "Serial Number",
      selector: (row) => row.serial_number,
      sortable: true,
    },
    {
      name: "Action(s)",
      selector: (row) => (
        <>
          {loadingComponent ? (
            <Icon name="spinner" color="green" loading />
          ) : (
            <PopupButton
              content="Remove"
              iconName="minus"
              color="teal"
              onClick={async () => {
                setLoadingComponent(true);
                const response = await agent.PCComponentInstance.setItem(
                  row.id,
                  {
                    inventory_item_id: null,
                  }
                );

                setDtAvailableComponents([...dtAvailableComponents, response]);
                setDtComponents(dtComponents.filter((a) => a.id !== row.id));
                setLoadingComponent(false);
              }}
            />
          )}
        </>
      ),
      right: true,
    },
  ];

  const columns2 = [
    {
      name: "Component",
      selector: (row) => row.component.name,
      sortable: true,
    },
    {
      name: "Brand",
      selector: (row) => row.brand,
      sortable: true,
    },
    {
      name: "Serial Number",
      selector: (row) => row.serial_number,
      sortable: true,
    },
    {
      name: "Action(s)",
      selector: (row) => (
        <>
          {loadingComponent ? (
            <Icon name="spinner" loading color="green" />
          ) : (
            <PopupButton
              content="Add"
              iconName="add"
              color="teal"
              onClick={async () => {
                setLoadingComponent(true);
                const response = await agent.PCComponentInstance.setItem(
                  row.id,
                  {
                    inventory_item_id: inventoryItem.id,
                  }
                );

                setDtComponents([...dtComponents, response]);
                setDtAvailableComponents(
                  dtAvailableComponents.filter((a) => a.id !== row.id)
                );
                setLoadingComponent(false);
              }}
            />
          )}
        </>
      ),
      right: true,
    },
  ];

  const [loading, setLoading] = useState(false);
  const [loadingComponent, setLoadingComponent] = useState(false);
  const [inventoryItem, setInventoryItem] = useState({
    name: "",
    parentName: "",
    id: 0,
  });

  const [dtAvailableComponents, setDtAvailableComponents] = useState([]);
  const [dtComponents, setDtComponents] = useState([]);

  useEffect(() => {
    loadData();
    loadAvailableComponents();
  }, []);

  const loadData = async () => {
    setLoading(true);
    const response = await agent.Inventory.itemShowComponents(id);
    setInventoryItem({
      name: response.serial_number,
      parentName: response.inventory_parent_item.name,
      id: response.id,
    });
    setDtComponents(response.components);
    setLoading(false);
  };

  const loadAvailableComponents = async () => {
    setLoadingComponent(true);

    const response = await agent.PCComponentInstance.available();
    setDtAvailableComponents(response);

    setLoadingComponent(false);
  };

  const openForm = () => {};

  const onSearch = (term) => {};

  return (
    <>
      <div>
        <div className="page-header-title">
          <span
            className="link"
            onClick={() => {
              console.log("going back");
              history.goBack();
            }}
          >
            {inventoryItem.parentName}{" "}
          </span>
          <Icon name="arrow right" />
          {inventoryItem.name} <Loader active={loading} inline size="tiny" />
        </div>
        <hr></hr>
      </div>

      <Segment.Group>
        <Segment>
          <Icon name="computer" />
          <b>COMPONENTS</b>
        </Segment>
        <Segment>
          <DataTable
            progressPending={loadingComponent && loading}
            striped
            columns={columns1}
            data={dtComponents}
          />
        </Segment>
      </Segment.Group>

      <Segment.Group>
        <Segment>
          <Icon name="computer" />
          <b>AVAILABLE COMPONENTS</b>
        </Segment>
        <Segment>
          <DataTable
            progressPending={loadingComponent && loading}
            striped
            columns={columns2}
            data={dtAvailableComponents}
          />
        </Segment>
      </Segment.Group>
    </>
  );
};
