import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { Button, Form, Modal } from "semantic-ui-react";
import modalActions from "../../actions/modalActions";
import agent from "../../agent";

export const JobOrderCreatePurchaseOrder = ({
  requestedItem,
  jobOrderId,
  onConfirm,
}) => {
  const { inventory_parent_item: itemParent } = requestedItem;

  const dispatch = useDispatch();
  const modal = useSelector((state) => state.modal);

  const inputFile = useRef(null);

  return (
    <>
      <Modal.Content>
        <Form>
          <Form.Field>
            <label>Item</label>
            <Form.Input readOnly value={itemParent.name} />
          </Form.Field>
          <Form.Field>
            <label>Purchase Order</label>
            <input type="file" ref={inputFile} />
          </Form.Field>
        </Form>
      </Modal.Content>
      <Modal.Actions>
        <Button
          negative
          loading={modal.loading}
          disabled={modal.loading}
          onClick={() => {
            modalActions.closeModal(dispatch);
          }}
        >
          Cancel
        </Button>
        <Button
          positive
          loading={modal.loading}
          onClick={async () => {
            modalActions.setErrors(dispatch, null);
            modalActions.setLoading(dispatch, true);

            try {
              // request here
              const file =
                inputFile.current.files.length > 0
                  ? inputFile.current.files[0]
                  : "";
              await agent.JobOrders.createPO(jobOrderId, file);

              onConfirm();
              toast.success("Purchase Order created");
              modalActions.closeModal(dispatch);
            } catch (err) {
              modalActions.setErrors(dispatch, err.data.errors);
              modalActions.setLoading(dispatch, false);
            }
          }}
        >
          OK
        </Button>
      </Modal.Actions>
    </>
  );
};
