import React, { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import { useDispatch } from "react-redux";
import { toast } from "react-toastify";
import { Button, Icon, Loader } from "semantic-ui-react";
import { history } from "../..";
import modalActions from "../../actions/modalActions";
import agent from "../../agent";
import { dateStringToLocal } from "../../helpers";
import { ConfirmationModal } from "../Commons/ConfirmationModal";
import { DelayedSearchInput } from "../Commons/DelayedSearchInput";
import { PopupButton } from "../Commons/PopupButton";
import { PCComponentForm } from "./PCComponentForm";

export const PCComponentContent = () => {
  const columns = [
    {
      name: "Name",
      selector: (row) => row.name,
      sortable: true,
    },
    {
      name: "Qty",
      selector: (row) => row.instances_id_only.length,
      sortable: true,
    },
    {
      name: "Date Created",
      selector: (row) => dateStringToLocal(row.created_at),
      sortable: true,
    },
    {
      name: "Actions",
      selector: (row) => (
        <>
          <PopupButton
            color="teal"
            iconName="eye"
            content="Show Components"
            onClick={() => {
              history.push(`/pc-components/${row.id}`);
            }}
          />
          <PopupButton
            color="teal"
            content="Edit Item"
            iconName="pencil"
            onClick={() => {
              modalActions.openModal(
                dispatch,
                "Edit " + row.name,
                <PCComponentForm onSave={loadData} id={row.id} record={row} />
              );
            }}
          />
          <PopupButton
            color="teal"
            content="Delete Item"
            iconName="trash"
            onClick={() => {
              modalActions.openModal(
                dispatch,
                "Delete " + row.name,
                <ConfirmationModal
                  content="Confirm delete?"
                  onSubmit={async () => {
                    modalActions.setLoading(dispatch, true);

                    await agent.PCComponent.delete(row.id);

                    toast.success("Deleted");
                    loadData();

                    modalActions.closeModal(dispatch);
                  }}
                />
              );
            }}
          />
        </>
      ),
      right: true,
    },
  ];

  const dispatch = useDispatch();

  const [loading, setLoading] = useState(false);

  const [data, setData] = useState([]);
  const [dataTemp, setDataTemp] = useState([]);

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    setLoading(true);
    const response = await agent.PCComponent.list();
    setData(response);
    setDataTemp(response);
    setLoading(false);
  };

  const openForm = () => {
    modalActions.openModal(
      dispatch,
      "Create New PC Component",
      <PCComponentForm onSave={loadData} />
    );
  };

  const onSearch = (term) => {
    setData(
      dataTemp.filter((d) => {
        return d.name.includes(term);
      })
    );
  };

  return (
    <>
      <div>
        <div className="page-header-title">
          PC COMPONENTS <Loader active={loading} inline size="tiny" />
        </div>
        <hr></hr>
      </div>

      <div className="mb-10 clearfix">
        <div className="disp-ib">
          <DelayedSearchInput onSearch={onSearch} />
        </div>
        <div className="float-r disp-ib">
          <Button
            size="small"
            color="green"
            onClick={() => {
              openForm();
            }}
          >
            <Icon name="add" /> Create Item
          </Button>
        </div>
      </div>

      <DataTable columns={columns} data={data} pagination striped />
    </>
  );
};
