import React, { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import agent from "../../agent";
import { dateStringToLocal } from "../../helpers";
import { downloadBase64File } from "../../libs/download";
import { PopupButton } from "../Commons/PopupButton";

export const DisposedItemsComponent = () => {
  const columns = [
    {
      name: "Item",
      selector: (row) => row.item_name,
      sortable: true,
    },
    {
      name: "Date Disposed",
      selector: (row) => row.date_disposed,
      sortable: true,
      right: true,
    },
  ];

  const [dtData, setDtData] = useState([]);
  const [loading, setLoading] = useState(false);

  const loadData = async () => {
    setLoading(true);

    let response = await agent.Inventory.diposedItemList();

    response = response.map((a) => {
      return {
        item_name: (
          <>
            {" "}
            <div style={{ fontWeight: "bold" }}>
              {a.inventory_parent_item.name}
            </div>
            <div style={{ color: "grey" }}>{a.serial_number}</div>
          </>
        ),
        date_disposed: dateStringToLocal(a.updated_at),
      };
    });

    setDtData(response);

    setLoading(false);
  };

  useEffect(() => {
    loadData();
  }, []);

  return (
    <DataTable
      striped
      progressPending={loading}
      columns={columns}
      data={dtData}
    />
  );
};
