import React, { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import { useDispatch } from "react-redux";
import { toast } from "react-toastify";
import { Loader } from "semantic-ui-react";
import modalActions from "../../actions/modalActions";
import agent from "../../agent";
import { dateStringToLocal } from "../../helpers";
import { ConfirmationModal } from "../Commons/ConfirmationModal";
import { LabelJobOrderStatus } from "../Commons/LabelJobOrderStatus";
import { PopupButton } from "../Commons/PopupButton";
import { JobOrderCreatePurchaseOrder } from "./JobOrderCreatePurchaseOrder";
import { JobOrderReplaceItemForm } from "./JobOrderReplaceItemForm";

export const JobOrderComponent = () => {
  const columns = [
    {
      name: "Created By",
      selector: (row) => row.requestor_name,
      sortable: true,
    },
    {
      name: "Item",
      selector: (row) => row.item_name,
      sortable: true,
    },
    {
      name: "Status",
      selector: (row) => row.status,
      sortable: true,
    },
    {
      name: "Date",
      selector: (row) => row.created_at,
      sortable: true,
    },
    {
      name: "Actions",
      selector: (row) => row.actions,
      right: true,
    },
  ];

  const dispatch = useDispatch();
  const [loading, setLoading] = useState(false);
  const [dtData, setDtData] = useState([]);

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    setLoading(true);

    const response = await agent.JobOrders.listPending();

    setDtData(
      response.map((a) => {
        return {
          ...a,
          status: <LabelJobOrderStatus status={a.status} />,
          created_at: dateStringToLocal(a.created_at),
          requestor_name: a.handler.name,
          item_name: (
            <>
              <div>{a.repair_request.item.inventory_parent_item.name}</div>
              <div style={{ color: "grey" }}>
                {a.repair_request.item.serial_number}
              </div>
            </>
          ),
          actions: (
            <>
              <PopupButton
                content="Mark as repaired"
                iconName="check"
                color="green"
                onClick={() => {
                  modalActions.openModal(
                    dispatch,
                    "Mark Item as Repaired",
                    <ConfirmationModal
                      content="Set this Item as repaired. Click OK to proceed"
                      onSubmit={async () => {
                        modalActions.setLoading(dispatch, true);

                        await agent.JobOrders.repair(a.id);

                        modalActions.closeModal(dispatch);
                        loadData();
                        toast.success("Item has been marked as repaired");
                      }}
                    />
                  );
                }}
              />

              <PopupButton
                content="Replace Item"
                iconName="undo"
                color="teal"
                onClick={() => {
                  modalActions.openModal(
                    dispatch,
                    `Replace ${a.repair_request.item.inventory_parent_item.name} (${a.repair_request.item.serial_number})`,
                    <JobOrderReplaceItemForm
                      itemParentId={
                        a.repair_request.item.inventory_parent_item.id
                      }
                      requestedItemId={a.repair_request.item.id}
                      jobOrderId={a.id}
                      onConfirm={() => {
                        loadData();
                      }}
                    />
                  );
                }}
              />

              <PopupButton
                content="Create Purchase Order"
                iconName="file alternate"
                color="orange"
                onClick={() => {
                  modalActions.openModal(
                    dispatch,
                    `Create Purchase Order`,
                    <JobOrderCreatePurchaseOrder
                      requestedItem={a.repair_request.item}
                      jobOrderId={a.id}
                      onConfirm={() => {
                        loadData();
                      }}
                    />
                  );
                }}
              />
            </>
          ),
        };
      })
    );

    setLoading(false);
  };

  return (
    <>
      <div>
        <div className="page-header-title">
          JOB ORDERS <Loader active={loading} inline size="tiny" />
        </div>
        <hr></hr>
      </div>
      <DataTable columns={columns} data={dtData} pagination />
    </>
  );
};
