import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { Button, Form, Modal } from "semantic-ui-react";
import modalActions from "../../actions/modalActions";
import agent from "../../agent";

export const PCComponentInstanceForm = ({
  onSave,
  componentId,
  id = 0,
  record,
}) => {
  const dispatch = useDispatch();
  const modal = useSelector((state) => state.modal);

  const initialForm = {
    brand: "",
    serial_number: "",
    pc_component_id: componentId,
  };

  const [formData, setFormData] = useState(
    id === 0 ? { ...initialForm } : { ...record, pc_component_id: componentId }
  );

  const handleFormInput = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const saveForm = async () => {
    modalActions.setLoading(dispatch, true);

    try {
      if (id === 0) await agent.PCComponentInstance.create({ ...formData });
      else await agent.PCComponentInstance.update(id, { ...formData });

      if (onSave) onSave();

      toast("PC Component Created");
      modalActions.closeModal(dispatch);
    } catch (err) {
      modalActions.setErrors(dispatch, err.data.errors);
      modalActions.setLoading(dispatch, false);
    }
  };

  return (
    <>
      <Modal.Content>
        <Form>
          <Form.Field>
            <label>Serial Number</label>
            <Form.Input
              name="serial_number"
              value={formData.serial_number}
              onChange={handleFormInput}
              placeholder="Serial Number"
            ></Form.Input>
          </Form.Field>
          <Form.Field>
            <label>Brand</label>
            <Form.Input
              name="brand"
              value={formData.brand}
              onChange={handleFormInput}
              placeholder="Brand"
            ></Form.Input>
          </Form.Field>
        </Form>
      </Modal.Content>

      <Modal.Actions>
        <Button
          negative
          loading={modal.loading}
          disabled={modal.loading}
          onClick={() => {
            modalActions.closeModal(dispatch);
          }}
        >
          Cancel
        </Button>
        <Button
          positive
          loading={modal.loading}
          disabled={modal.loading}
          onClick={saveForm}
        >
          OK
        </Button>
      </Modal.Actions>
    </>
  );
};
